.navbar-desktop {
  text-transform: uppercase;
  display: none;
  position: relative;
}
@media screen and (min-width: 64rem) {
  .navbar-desktop {
    display: flex;
  }
}
.navbar-desktop .navbar-desktop-wrapper {
  background-color: var(--warna-cream);
  max-width: 100vw;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  padding: 1rem 0;
  z-index: 2;
  box-shadow: 0 2px 10px 2px rgba(253, 247, 242, 0.2);
}
.navbar-desktop .navbar-desktop-wrapper .navbar-desktop-content {
  max-width: 80rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.navbar-desktop .navbar-desktop-wrapper .navbar-desktop-content h1 {
  margin-left: 2rem;
}
.navbar-desktop .navbar-desktop-wrapper .navbar-desktop-content .button-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 2rem;
}
.navbar-desktop .navbar-desktop-wrapper .navbar-desktop-content .button-wrapper .button {
  margin: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}
.navbar-desktop .navbar-desktop-wrapper .navbar-desktop-content .button-wrapper .button #button-container {
  z-index: 1;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  border-radius: 40px;
  padding: 0.5rem 2rem;
  border: 1px solid var(--warna-biru);
  transition: border 0.5s ease;
}
.navbar-desktop .navbar-desktop-wrapper .navbar-desktop-content .button-wrapper .button #button-container .letter {
  display: flex;
  flex-direction: row;
  font-size: 18px;
}
.navbar-desktop .navbar-desktop-wrapper .navbar-desktop-content .button-wrapper .button #button2-container {
  z-index: 0;
  position: absolute;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  padding: 0.5rem 2rem;
}
.navbar-desktop .navbar-desktop-wrapper .navbar-desktop-content .button-wrapper .button #button2-container .letter2 {
  display: flex;
  flex-direction: row;
  font-size: 18px;
}